#workshops {
    background-color: var(--light2);
    flex-direction: column;
    justify-content: center;
}

.cards {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    width: 80vw;
    padding-bottom: 50px;
}

.inviteCards {
    padding-bottom: 0;
}

.inviteCards .card {
    width: 80vw;
}

.card-footer {
    font-size: 0.9rem;
}
