#conferences {
    background-color: var(--accent);
    color: var(--light2);
}

#conferences .button {
    justify-content: flex-start;
    margin: 0;
}

#conferences .button-link:not(:hover) {
    background-color: var(--light2);
    color: var(--accent);
}

#conferences .card {
    width: 80vw;
}

#conferences .card-header {
    padding-left: 40px;
}

#conferences .card-title {
    justify-content: left;
    padding-left: 20px;
}

#conferences .card-subtitle {
    padding-left: 20px;
}

#conferences p {
    vertical-align: middle;
    font-size: 20px;
    padding-left: 20px;
    padding-right: 20px;
}
