:root {
    --dark2: black;
    --light1: rgba(236, 236, 236, .3);
    --light2: white;
    --light3: rgba(242, 241, 239, .8);
    --accent: #401365;
    --highlight: #F8D911;
    --schedule-color-1: var(--accent);
    --schedule-color-2: purple;
    --primary-font: 'Lexend Mega', sans-serif;
    --secondary-font: 'Nunito Sans' sans-serif;
}

body {
    width: 100%;
    font-family: var(--secondary-font);
}

.page {
    min-height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding-top: 15vh;
    padding-bottom: 5vh;
}

.title {
    font-family: var(--primary-font);
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
}

.date {
    font-family: var(--primary-font);
    text-transform: uppercase;
    font-size:1rem;
}

.details {
    width: 80vw;
    padding-top: 20px;
}

ul {
    list-style: none;
    padding-left: 0;
    padding-top: 2vh;
}

li {
    text-align: justify;
    padding: 0.5vh;
}

.button {
    display: flex;
    justify-content: center;
    padding-top: 20px;
    margin: auto;
}

.button-link {
    background-color: var(--accent);
    color: var(--light);
    padding: 8px 30px;
    text-transform: uppercase;
    border-radius: 4px;
    transition-duration: 0.3s;
}

.button-link:hover {
    background: var(--schedule-color-2);
    text-decoration: none;
    color: var(--light2);
}

.h6, h6 {
    font-size: 0.9rem!important;
}

.text-muted {
    font-family: var(--primary-font);
}

.card {
    display: flex;
    justify-content: space-between;
    width: 380px;
    margin: 25px 10px;
    box-shadow: 0 4px 8px 0 var(--accent), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border-radius: 2px;
}

.card-header {
    text-transform: uppercase;
    font-size: 0.8rem;
}

.card-title {
    font-family: var(--primary-font);
    display: flex;
    justify-content: center;
    align-items: center;
    vertical-align: middle;
    text-align: center;
}

.card-subtitle {
    padding-top: 10px;
    padding-bottom: 10px;
}

.card-text {
    vertical-align: middle;
    text-align: justify;
    font-size: 1rem;
}
