@media (max-width: 1100px) {
    #about-us .box {
        width: 90vw;
    }

    .card {
        width: 80vw;
    }

    #hackathon .info {
        flex-direction: column;
    }

    #hackathon .info .detailsList {
        width: auto;
    }
}

@media (max-width: 768px) {
    #NavBar:not(.hidden) {
        position: relative;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        background: var(--dark2);
        z-index: 10;
        width: 100vw;
    }

    .box {
        padding: 6vw;
    }

    #home .title {
        font-size: 8vw;
    }

    #home .subtitle {
        font-size: 4vw;
    }

    .button-link {
        font-size: 2.5vw;
    }
}

@media (max-width: 650px) {
    .winners-podium {
        display: flex;
        flex-direction: column;
    }

    .gray1, .gray2, .gray3 {
        display: none;
    }

    #contact {
        justify-content: center;
    }

    .sponsorList {
        padding-top: 25px;
    }

    .sponsor {
        padding: 20px;
    }

    .sponsorImage {
        height: 6vh;
    }

    .partnerList {
        padding-bottom: 25px;
    }

    .partnerImage {
        height: 6vh;
    }
}

@media (max-width: 450px) {
    .membersList {
        padding: 2vw;
    }

    .memberImage {
        width: 70vw;
    }

    .memberName {
        font-size: 5vw;
    }

    .contactLink {
        font-size: 4vw;
        padding: 1vw;
    }

    .collaboratorName {
        font-size: 3.5vw;
    }

    .sponsorList {
        padding-top: 20px;
    }

    .sponsor {
        padding: 20px;
    }

    .sponsorImage {
        height: 5vh;
    }

    .partnerList {
        padding-bottom: 20px;
    }

    .partnerImage {
        height: 5vh;
    }

    .logoUB {
        width: 50vw;
        height: auto;
    }
}
